<template>
  <div class="tab-category">
    <common-empty v-if="!cateList.length && fetchend"/>
    <div v-else>
      <div class="category-item" v-for="(item) in cateList" :key="item.id" @click="goCategory(item)">
        <div class="tag"></div>
        <div class="category-name">{{item.cateName}}</div>
        <van-icon name="arrow" color="#333"/>
      </div>
    </div>
  </div>
</template>
<script>
import {
  getCate
} from '@/http/service/shop';
import commonEmpty from '../commonEmpty';
/** 千店千面 底部tab 商品分类 */
export default {
  props: {
    storeId: {
      type: String,
      default: '',
    },
    storeSign: {
      type: Object,
      default: () => {},
    },
  },
  components: {
    commonEmpty,
  },
  data() {
    return {
      fetchend: false,
      cateList: [],
    };
  },
  mounted() {
    this.getCates();
  },
  computed: {
    location() {
      return this.$store.getters['getlocationInfo']
    },
  },
  methods: {
    goCategory(item) {
      this.$router.push(`/goodsSearch?visitStore=${this.storeId}&productCateId=${item.id}&storeType=${this.storeSign.storeType}`);
    },
    getCates() {
      let param = {
        storeId: this.storeId, // '1821113113'
        provinceId: this.location.provinceId,
        cityId: this.location.cityId,
        regionId: this.location.districtId,
        streetId: this.location.townshipId,
      }

      getCate(param).then(res => {
        if (res.success) {
          this.cateList = res.result
          this.fetchend = true;
          // this.cateList.unshift({ id: 0, parentId: 0, cateName: '全部', sortNum: -1, lastModifyTime: 0 })
        }
      })
    }
  },
}
</script>
