<template>
    <div class="want-btn-container" :class="{'gray': type === 'gray','blue': type === 'blue','white': type === 'white'}">
        <div class="want-btn" @click="toFeedBack" v-if="isHaierApp">
            <span class="icon"></span>
            <span class="text">把你想要的告诉我们</span>
        </div>
        <div class="want-text" v-else><span>没有更多啦~</span></div>
    </div>
</template>

<script>
export default {
  props: ['type', 'classList'],
  data() {
    return {
      isHaierApp: false, // 验证是否智家app内
    }
  },
  created() {
    this.isHaierApp = this.$util.isUplus()
  },
  methods: {
    /**
     * 跳转反馈页
    */
    toFeedBack() {
      // 验证登录
      if (this.$store.getters.getLoginStatus) {
        this.$util.openNewWindow('https://uplus.haier.com/uplusapp/problemFeedBack/feedback.html', true, false, false)
      } else {
        this.$store.dispatch('doLogin').then(res => {
          if (res === 400) {
            console.log('请求异常')
          }
        })
      }
    },
  }
}
</script>

